<template>
  <div class="tab-bar-item" @click="itemClick">
    <div :class="activeClass" :style="activeStyle">
      <slot name="item-text"></slot>
    </div>
  </div>
</template>

<script>
  export default {
    name: "TabBarItem",
    props:{
      path:String,
      activeColor:{
        type:String,
        default:'#333'
      },
      activeBorderBottom:{
        type:String,
        default:"tab-bar-item1"
      }
    },
    data(){
      return{
        // isActive:false
      }
    },
    computed:{
      isActive(){
        return this.$route.path.indexOf(this.path)!== -1
      },
      activeStyle(){
        return this.isActive ? {color:this.activeColor}:{}
      },
      activeClass(){
        return this.isActive ? this.activeBorderBottom:""
      }
    },
    methods:{
      itemClick(){
        this.$router.replace(this.path)
      }
    }
  }
</script>

